import React, { useEffect, useState } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { useTheme, Box, Tab, Tabs, useMediaQuery, Button, Typography, ImageList, Grid2, List, ListItem, IconButton} from '@mui/material';
import ChatBox from '../components/Chatbox';
import Stepper from '../components/Stepper';
import CloseIcon from '@mui/icons-material/Close';
import { useNavigate, useParams } from 'react-router-dom';
import { useOrderCancelMutation, useOrderDetailsLoadMutation, useOrderMockPaymentCompletedMutation } from '../services/orders';
import { useAppSelector } from '../hooks';
import { selectOrderDetails, selectOrderMaterials, selectOrderMessages, selectOrderVendor, selectOrderPlan, selectOrderProducts } from './orderSlice';
import { ResourceProductsContainer } from '../components/ResourceProductsContainer';
import { VendorCardV2 } from '../components/VendorCard';
import { PlanListItemElV2 } from '../components/PlanList';
import { OrderStatus, ResourceListItem, SalesOrderProduct } from '../types';
import { ArrowBack, Payment } from '@mui/icons-material';
import PaymentForm from '../components/PaymentForm';
import { OrderPaymentTotals } from '../components/OrderPaymentTotals';
import { calculateOrderTotals } from '../common/util';

function a11yProps(index: number) {
	return {
		id: `simple-tab-${index}`,
		'aria-controls': `simple-tabpanel-${index}`,
	};
}

interface TabPanelProps {
	children?: React.ReactNode;
	index: number;
	value: number;
}

function CustomTabPanel(props: TabPanelProps) {
	const { children, value, index, ...other } = props;
	return (
		<Box
			flex={1}
			// overflow='auto'
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
			{...other}
		>
			{children}
		</Box>
	);
}

interface PurchaseOrderResourceListProps {
	resources: ResourceListItem[];
	products: SalesOrderProduct[];
	orderStatus: OrderStatus;
	readOnly?: boolean;
	disableExpand?: boolean
}

export const PurchaseOrderResourceList = ({ resources, products, disableExpand }: PurchaseOrderResourceListProps ) => {
	return <Grid2 container spacing={1} mt={1}>
		{resources.map((resource, i) => {
			const resourceProducts = products.filter(({resourceId}) => resourceId === resource.id);
			return <Grid2 size={12} key={i}>
				<ResourceProductsContainer
					resource={resource}	
					products={resourceProducts}
					readOnly
					disableExpand={disableExpand}
				/>
			</Grid2>;
		})}
	</Grid2>;
};

const OrderDetailView = () => {
	useAuthEffects();

	const {id} = useParams();
	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const [orderDetailsLoadMutation] = useOrderDetailsLoadMutation();
	const [orderCancelMutation] = useOrderCancelMutation();
	const [orderMockPaymentCompletedMutation, { isSuccess: isPaymentSuccess }] = useOrderMockPaymentCompletedMutation();
	const [tabValue, setTabValue] = React.useState(0);
	const order = useAppSelector(selectOrderDetails);
	const materials = useAppSelector(selectOrderMaterials);
	const messages = useAppSelector(selectOrderMessages);
	const vendor = useAppSelector(selectOrderVendor);
	const plan = useAppSelector(selectOrderPlan);
	const products = useAppSelector(selectOrderProducts);
	const navigate = useNavigate();
	const [isCheckoutView, setIsCheckoutView] = useState<boolean>(false);

	useEffect(() => {
		if (id) {
			orderDetailsLoadMutation(id);
		}
	}, [id]);

	useEffect(() => {
		setIsCheckoutView(false);
	}, [isPaymentSuccess]);

	const handleTabChange = (event: React.SyntheticEvent, newValue: number) => {
		if (newValue === 1) {
			setTabValue(0);
		}
		setTabValue(newValue);
	};

	const handleCancelOrderClick = () => {
		if (id) {
			orderCancelMutation(id);
		}
	};

	const handlePaymentSubmit = () => {
		if (id) {
			orderMockPaymentCompletedMutation(id);
		}
	};

	const {subTotal, tax, shipping, total} = calculateOrderTotals(products);

	const isStatusPaidOrAfter = order && [OrderStatus.Paid, OrderStatus.Shipped].includes(order.status) ? true : false;

	if (isCheckoutView) {
		return <PageWrapper>
			<Box sx={{mt: 4, mb: 2}}>
				{order && <Stepper 
					orderStatus={order.status}
					orderDate={order.createdAt}
					inProgress
				/>}
			</Box>
			<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
				<IconButton onClick={() => setIsCheckoutView(false)} sx={{my:0.5}}>
					<ArrowBack />
				</IconButton>
			</Box>
			<Grid2 container spacing={2}>
				<Grid2 size={isSmUp ? 6 : 12}>
					{order?.status === OrderStatus.Prepared && <PurchaseOrderResourceList
						resources={materials}
						products={products}
						orderStatus={order.status}
						disableExpand
					/>}
					<OrderPaymentTotals 
						subTotal={subTotal}
						tax={tax}
						shipping={shipping}
						total={total}
						isPaid={isStatusPaidOrAfter}
					/>
				</Grid2>
				<Grid2 size={isSmUp ? 6 : 12}>
					<PaymentForm total={total} onSubmit={handlePaymentSubmit} />
				</Grid2>
			</Grid2>
		</PageWrapper>;
	}

	return <PageWrapper noScroll={tabValue === 1} disableFooter={tabValue === 1}>
		<Box sx={{mt: 4, mb: 2}}>
			{order && <Stepper 
				orderStatus={order.status}
				orderDate={order.createdAt}
			/>}
		</Box>
		<Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
			<Tabs value={tabValue} onChange={handleTabChange} aria-label="basic tabs example">
				<Tab label="Items" {...a11yProps(0)} />
				<Tab label="Messages" {...a11yProps(1)} />
			</Tabs>
		</Box>
		<CustomTabPanel value={tabValue} index={0}>
			{order && [OrderStatus.Created, OrderStatus.Cancelled].includes(order.status) && <PurchaseOrderResourceList 
				resources={materials}
				products={[]}
				readOnly
				orderStatus={order.status}
			/>}
			{order && [OrderStatus.Prepared, OrderStatus.Paid, OrderStatus.Shipped].includes(order.status) && <PurchaseOrderResourceList
				resources={materials}
				products={products}
				readOnly
				orderStatus={order.status}
			/>}
			{order?.status === OrderStatus.Created && <Grid2 container my={1}>
				<Grid2 size={isSmUp ? 'grow' : 12} my={1}>
					You&apos;ll be notified when the store has accepted your offer!
				</Grid2>
				<Grid2 size={isSmUp ? 'auto' : 12}>
					<Button size='large' startIcon={<Payment />} disabled>To checkout</Button>
					<Button size='large' onClick={() => handleCancelOrderClick()} startIcon={<CloseIcon />} color='error'>Cancel order</Button>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Prepared && <Grid2 container>
				<Grid2 size={isSmUp ? 12 : 12}>
					<List sx={{pb:0}}>
						<ListItem>
							<Typography flex={1}>Subtotal</Typography>
							<Typography>€{subTotal}</Typography>
						</ListItem>
					</List>
				</Grid2>
			</Grid2>}
			{isStatusPaidOrAfter && <Grid2 container>
				<Grid2 size={isSmUp ? 12 : 12}>
					<OrderPaymentTotals 
						subTotal={subTotal}
						tax={tax}
						shipping={shipping}
						total={total}
						isPaid={isStatusPaidOrAfter}
					/>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Prepared && <Grid2 container my={1} justifyContent='right'>
				<Grid2>
					<Button size='large' onClick={() => setIsCheckoutView(true)} startIcon={<Payment />}>To checkout</Button>
					{/* <Button size='large' onClick={() => handleCancelOrderClick()} startIcon={<CloseIcon />} color='error'>Cancel order</Button> */}
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Paid && <Grid2 container my={1}>
				<Grid2>
					<Typography my={1}>Your items are getting ready to be shipped! 🙌</Typography>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Shipped && <Grid2 container my={1}>
				<Grid2>
					<Typography my={1}>Thank you for building with us!</Typography>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Cancelled && <Grid2 container my={1}>
				<Grid2>
					<Typography my={1}>You&apos;ve cancelled this order.</Typography>
				</Grid2>
			</Grid2>}
			{order?.status === OrderStatus.Rejected && <Grid2 container my={1}>
				<Grid2>
					<Typography my={1}>The vendor has rejected this order.</Typography>
				</Grid2>
			</Grid2>}
			<Grid2 container mt={4} spacing={2}>
				<Grid2 size={isSmUp ? 12 : 12}>
					<Typography variant='body2'>
						Plan
					</Typography>
					{plan && <ImageList gap={8} cols={1} sx={{mt:1}}>
						<PlanListItemElV2
							key={plan.id}
							item={plan}
						/>
					</ImageList>}
				</Grid2>
				<Grid2 size={isSmUp ? 12 : 12}>
					<Typography variant='body2'>
						Vendor
					</Typography>
					<Box mt={1}>
						{vendor && <VendorCardV2 
							vendor={vendor}
						/>}
					</Box>
				</Grid2>
			</Grid2>
		</CustomTabPanel>
		<CustomTabPanel value={tabValue} index={1}>
			{order && <ChatBox orderId={order.id} messages={messages} isOpen={tabValue===1} />}
		</CustomTabPanel>
	</PageWrapper>;
};

export default OrderDetailView;
