import { Box, ListItem, ListItemAvatar, Avatar, alpha, ListItemText, Collapse, useTheme, Button, IconButton, Card, Checkbox, ListItemButton } from '@mui/material';
import React, { ReactNode, useEffect, useMemo } from 'react';
import { OrderProduct, ResourceListItem } from '../types';
import CategoryIcon from '@mui/icons-material/Category';
import { Add as AddIcon, Check, Delete as DeleteIcon } from '@mui/icons-material';
import KeyboardArrowUpIcon from '@mui/icons-material/KeyboardArrowUp';
import KeyboardArrowDownIcon from '@mui/icons-material/KeyboardArrowDown';
import UnfoldMoreIcon from '@mui/icons-material/UnfoldMore';
import UnfoldLessIcon from '@mui/icons-material/UnfoldLess';

const ListItemContainer = ({children, onClick}:{children: ReactNode, onClick?: () => void}) => onClick ? <ListItemButton sx={{p:0}} onClick={onClick}>{children}</ListItemButton> : <ListItem component='div' sx={{p:0}}>{children}</ListItem>;

interface ResourceListItemProps { 
	resource: ResourceListItem,
	onClick?: () => void,
	open?: boolean;
	onExpandToggle?: () => void
	onCheckboxChange?: () => void;
	isChecked?: boolean;
	secondaryText?: string; 
}

const ResourcesListItem = ({ resource, onClick, onExpandToggle, open, onCheckboxChange, isChecked, secondaryText }: ResourceListItemProps) => {
	const handleCheckboxChange = onCheckboxChange ? (e: React.ChangeEvent<HTMLInputElement>) => {
		e.stopPropagation();
		onCheckboxChange();
	} : undefined;
	const theme = useTheme();
	return <ListItemContainer onClick={onClick}>
		<ListItemAvatar>
			<Avatar src={resource?.imgUrl} variant='square' sx={{ width: 96, height: 96, borderBottomLeftRadius: 0, background: alpha(theme.palette.getContrastText(theme.palette.background.default), 0.12), color: 'divider' }}>
				<CategoryIcon fontSize='large'  />
			</Avatar>
		</ListItemAvatar>
		<ListItemText
			sx={{
				flex: 1,
				px: 2,
				'& > p': {color: 'primary.main'},
			}}
			primary={resource.name}
			secondary={secondaryText}
		/>
		<ListItemText
			sx={{
				flex: 'unset',
				'& > span': {display:'inline-block', whiteSpace: 'nowrap', mr: 0.5},
				'& > p': {display:'inline-block'},
			}}
			primary={resource.quantity}
			secondary={resource.uom}
		/>
		<Box pr={2}>
			{onCheckboxChange && <Checkbox
				onChange={handleCheckboxChange}
				checked={isChecked}
			/>}
		</Box>
		{onExpandToggle && <Box p={2}>
			<IconButton onClick={() => onExpandToggle()}>
				{open ? <KeyboardArrowUpIcon /> : <KeyboardArrowDownIcon />}
			</IconButton>
		</Box>}
	</ListItemContainer>;
};

interface ProductListItemProps { 
	product: OrderProduct,
	open: boolean,
	onClick?: () => void;
	onExpandToggle?: () => void,
	onDeleteClick?: (productId: number) => void,
	showSku?: boolean;
}

export const ProductsListItem = ({ product, open, onExpandToggle, onDeleteClick, onClick, showSku}: ProductListItemProps) => {
	const theme = useTheme();
	return <ListItemContainer onClick={onClick}>
		<ListItemAvatar>
			<Avatar src={product?.imgUrl} variant='square' sx={{ width: 96, height: 96, borderBottomLeftRadius: 0, background: alpha(theme.palette.getContrastText(theme.palette.background.default), 0.12), color: 'divider' }}>
				<CategoryIcon fontSize='large'  />
			</Avatar>
		</ListItemAvatar>
		<ListItemText
			sx={{
				flex: 1,
				px: 2,
				'& > p': {color: 'primary.main'},
			}}
			primary={product.name}
			secondary={`€${product.pricePerUnit}`}
		/>
		{showSku && <ListItemText
			sx={{
				flex: 'unset',
				px: 2
			}}
			primary={product.sku}
		/>}
		<ListItemText
			sx={{
				flex: 'unset',
				px: 2,
				'& > span': {display:'inline-block', whiteSpace: 'nowrap', mr: 0.5},
				'& > p': {display:'inline-block'},
			}}
			primary={product.quantity}
			secondary={product.uom}
		/>
		{onDeleteClick && <Box p={2}>
			<IconButton onClick={() => onDeleteClick(product.id)}><DeleteIcon fontSize='small' /></IconButton>
		</Box>}
		{onExpandToggle && <Box p={2}>
			<IconButton
				aria-label="expand row"
				size="small"
				onClick={onExpandToggle}
			>
				{open ? <UnfoldLessIcon /> : <UnfoldMoreIcon />}
			</IconButton>
		</Box>}
	</ListItemContainer>;
};

interface ResourceProductsContainerProps {
	resource: ResourceListItem;
	products: OrderProduct[];
	onAddProductClick?: (resource: ResourceListItem) => void;
	onProductDeleteClick?: (productId: number) => void;
	onAddSuccessEffect?: object;
	readOnly?: boolean;
	disableExpand?: boolean;
	onResourceCheckboxChange?: () => void;
	isResourceChecked?: boolean;
	onResourceClick?: () => void;
	resourceSecondaryText?: string;
	showProductSku?: boolean;
}

export const ResourceProductsContainer = ({disableExpand, resource, products, onAddProductClick, onProductDeleteClick, readOnly, onResourceCheckboxChange, isResourceChecked, onResourceClick, resourceSecondaryText, showProductSku}: ResourceProductsContainerProps) => {
	const theme = useTheme();
	const hasProducts = useMemo(() => Boolean(products.length), [products]);
	const [open, setOpen] = React.useState(!hasProducts);
	const isExpanded = open;

	const onProductClick = !open ? () => setOpen(!open) : undefined;

	// automatically close container in case first product added
	useEffect(() => {
		if (hasProducts) {
			setOpen(false);
		}
	}, [hasProducts]); 

	return <>
		{!disableExpand && <Collapse in={isExpanded && hasProducts}><Box mt={1}></Box></Collapse>}
		{!disableExpand && <Collapse in={isExpanded}>
			<Card sx={{borderRadius: 2}} elevation={onResourceClick ? 1 : 0}>
				<ResourcesListItem 
					resource={resource}
					onExpandToggle={hasProducts ? () => setOpen(!open) : undefined}
					open={open} 
					onCheckboxChange={onResourceCheckboxChange}
					isChecked={isResourceChecked}
					onClick={onResourceClick}
					secondaryText={resourceSecondaryText}
				/>
			</Card>
		</Collapse>}
		{products.map((product, i) => {
			return <Box key={i}>
				<Collapse in={!(i===0 && !isExpanded) && !(disableExpand && i===0)}>
					<Box mx={1} p={0.5} sx={{bgcolor: theme.palette.action.disabledBackground}} />
				</Collapse>
				<Card sx={{borderRadius: 2}} elevation={disableExpand || isExpanded ? 0 : 1}>
					<ProductsListItem
						product={product}
						open={open}
						onDeleteClick={(!readOnly && open) ? onProductDeleteClick : undefined}
						onClick={!disableExpand ? onProductClick : undefined}
						showSku={showProductSku}
					/>
				</Card>
			</Box>;
		})}
		{!readOnly && <Box display='flex' alignItems='center' justifyContent='space-between' m={1}>
			{hasProducts && !open && <Button startIcon={<Check color='success' />} color='inherit' onClick={() => setOpen(true)}>{resource.name}</Button>} 
			{hasProducts && open && <Button startIcon={<AddIcon />} color='inherit' onClick={onAddProductClick ? () => onAddProductClick(resource) : undefined}>Add another item</Button>}
			{!hasProducts && onAddProductClick && <Button startIcon={<AddIcon />} color='inherit' onClick={onAddProductClick ? () => onAddProductClick(resource) : undefined}>Add item</Button> }
		</Box>}
		{!disableExpand && <Collapse in={isExpanded && hasProducts}><Box mt={1}></Box></Collapse>}
	</>;
};
