import { Button, Typography, Card, CardActions, CardContent, CardMedia } from '@mui/material';
import React from 'react';
import { VendorListItem } from '../types';
import {Done as DoneIcon} from '@mui/icons-material';

interface Props {
    vendor: VendorListItem,
    onClick?: () => void;
    onButtonClick?: (e: any) => void;
    selected: boolean;
}

/**
 * @deprecated use VendorCardV2
 */
export const VendorCard = ({ vendor, selected, onClick, onButtonClick}: Props) => {
	return <Card 
		sx={{ maxWidth: 375, width: '100%', opacity: !selected  ? 0.3 : 1, cursor: 'pointer', userSelect: 'none', borderRadius: 2 }}
		onClick={onClick}
	>
		<CardMedia
			component="img"
			alt="vendor profile pic"
			height="140"
			image={vendor.imgUrl}
		/>
		<CardContent>
			<Typography gutterBottom variant="h5" component="div">
				{vendor.name}
			</Typography>
			<Typography variant="body2" sx={{ color: 'text.secondary' }}>
				{vendor.bio}
			</Typography>
		</CardContent>
		<CardActions>
			<Button
				startIcon={!selected ? null : <DoneIcon />} 
				size="small" 
				onClick={onButtonClick}>
				{selected ? 'Selected' : 'Tap to select'}
			</Button>
		</CardActions>
	</Card>;
};

interface PropsV2 {
    vendor: VendorListItem,
    onClick?: () => void;
    dim?: boolean;
    actions?: React.ReactNode;
}

export const VendorCardV2 = ({ vendor, dim, onClick, actions}: PropsV2) => {
	return <Card 
		sx={{ maxWidth: 375, width: '100%', opacity: dim ? 0.3 : 1, ...(onClick ? {cursor: 'pointer', userSelect: 'none'} : {}), borderRadius: 2 }}
		onClick={onClick}
	>
		<CardMedia
			component="img"
			alt="vendor profile pic"
			height="140"
			image={vendor.imgUrl}
		/>
		<CardContent>
			<Typography gutterBottom variant="h5" component="div">
				{vendor.name}
			</Typography>
			<Typography variant="body2" sx={{ color: 'text.secondary' }}>
				{vendor.bio}
			</Typography>
		</CardContent>
		{actions && <CardActions>
			{actions}
		</CardActions>}
	</Card>;
};