import { TAX_RATE } from '../constants';
import { SalesOrderProduct } from '../types';

const HEADER_HEIGHT = 56; 
const HEADER_HEIGHT_SM_UP = 64;

export const getHeaderHeight = (isSmUp: boolean) => isSmUp ? HEADER_HEIGHT_SM_UP : HEADER_HEIGHT;

export const screenHeightMinusHeaderStyle = (isSmUp: boolean) => `calc(100vh - ${getHeaderHeight(isSmUp)}px)`;

export const calculateOrderTotals = (products: SalesOrderProduct[]) => {
	const subTotal = products.reduce((acc, {pricePerUnit, quantity}) => acc+= Number(pricePerUnit)*Number(quantity), 0);
	const tax = (TAX_RATE * Number(subTotal));
	const shipping = 2.5;
	const total = subTotal + tax + shipping;
	return {subTotal, tax, shipping, total};
};