import React, { useEffect, useMemo, useState } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Avatar, Box, Button, Divider, FormControlLabel, FormGroup, Grid2, Switch, TextField, Typography, useTheme } from '@mui/material';
import { AccountBoxOutlined } from '@mui/icons-material';
import { useAppSelector } from '../hooks';
import { selectMyCompany, selectUserProfile } from './accountSlice';
import { MyCompany, useUpdateMyCompanyMutation } from '../services/accounts';

enum RolePreferenceProperty {
	Customer = 'roleCustomer',
	Creator = 'roleCreator',
	Vendor = 'roleVendor'

}

enum FORM_FIELDS {
	Name = 'name',
	Bio = 'bio'
}

const ProfileView = () => {
	useAuthEffects();
	const theme = useTheme();
	const myCompany = useAppSelector(selectMyCompany);
	const myAccount = useAppSelector(selectUserProfile);
	const [updateMyCompanyMutation] = useUpdateMyCompanyMutation();

	const handleRolePreferenceToggle = (key: RolePreferenceProperty) => {
		if (!myCompany) {
			return;
		}
		updateMyCompanyMutation({
			[key]: !myCompany[key],
		});
	};
	
	const [formData, setFormData] = useState<Partial<MyCompany>>({});

	const resetFormData = () => {
		if (!myCompany) {
			return;
		}
		const newFormData = Object.values(FORM_FIELDS).reduce((acc, key) => ({...acc, [key]: myCompany[key as keyof MyCompany]}), {});
		setFormData(newFormData);
	};

	useEffect(() => {
		resetFormData();
	}, [myCompany]);

	const handleFormFieldChange = (e: React.ChangeEvent<any>) => {
		setFormData({
			...formData,
			[e.target.name]: e.target.value
		});
	};

	const handleFormSubmit = (e: React.FormEvent<HTMLFormElement>) => {
		e.preventDefault();
		updateMyCompanyMutation(formData);
	};

	const hasFormChanged = useMemo(() => {
		if (!myCompany) {
			return false;
		}
		return Object.values(FORM_FIELDS).find((key) => formData[key] !== myCompany[key]) ? true : false;
	}, [formData, myCompany]);

	return <PageWrapper>
		{myCompany && <Grid2 container spacing={8}>
			<Grid2 size={12}>
				<Typography variant='h5'>Profile</Typography>
				<Divider sx={{mt: 1}}/>
				<form onSubmit={handleFormSubmit}>
					<Box sx={{marginY:2}}>
						<Avatar src={myCompany.imgUrl ?? undefined} sx={{ bgcolor: theme.palette.action.hover, color: theme.palette.text.primary, width: 196, height: 196, fontSize: 40, borderRadius: '5px' }} variant="square">
							<AccountBoxOutlined color='disabled' fontSize='large' />
						</Avatar>
					</Box>
					<Box sx={{marginY:2, maxWidth:540}}>
						<TextField name={FORM_FIELDS.Name} label="Name" variant="filled" fullWidth value={formData[FORM_FIELDS.Name] ?? ''} required autoComplete='off' onChange={handleFormFieldChange} />
					</Box>
					<Box sx={{marginY:2, maxWidth:540}}>
						<TextField name='region' label="E-mail" variant="filled" fullWidth value={myAccount?.email} disabled />
					</Box>
					<Box sx={{marginY:2, maxWidth:540}}>
						<TextField name='region' label="Region" variant="filled" fullWidth value='Global' disabled />
					</Box>
					<Box sx={{marginY:2, maxWidth:540}}>
						<TextField
							name={FORM_FIELDS.Bio}
							id="outlined-textarea"
							label="Bio"
							placeholder="A happy builder"
							multiline
							variant='filled'
							rows={4}
							fullWidth
							value={formData[FORM_FIELDS.Bio] ?? ''}
							onChange={handleFormFieldChange}
						/>
					</Box>
					<Button disabled={!hasFormChanged} variant='text' color='inherit' onClick={resetFormData}>Reset</Button>
				&nbsp;
					<Button type='submit' disabled={!hasFormChanged} variant='contained'>Save</Button>
				</form>
			</Grid2>
			<Grid2 size={12}>
				<Typography variant='h5'>Role preferences</Typography>
				<Divider sx={{mt: 1}}/>
				<Box sx={{marginY:2}}>
					<FormGroup>
						<FormControlLabel control={<Switch checked={myCompany.roleCustomer} onChange={() => handleRolePreferenceToggle(RolePreferenceProperty.Customer)} />} label="I am a customer" />
						{/* <FormControlLabel control={<Switch checked={myCompany.roleCreator} onChange={() => handleRolePreferenceToggle(RolePreferenceProperty.Creator)} />} label="I create DIY content" /> */}
						<FormControlLabel control={<Switch checked={myCompany.roleVendor} onChange={() => handleRolePreferenceToggle(RolePreferenceProperty.Vendor)} />} label="I sell supplies" />
					</FormGroup>
				</Box>
			</Grid2>
		</Grid2> }
	</PageWrapper>;
};

export default ProfileView;
