import React from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { Divider, Grid2, useMediaQuery, useTheme, Box } from '@mui/material';
import { useRemoveFavouriteMutation } from '../services/plans';
import { useAppSelector } from '../hooks';
import { selectFavourites } from './planSlice';
import { screenHeightMinusHeaderStyle } from '../common/util';
import PlanList, {PlanListItemEl} from '../components/PlanList';

const DiscoverView = () => {
	useAuthEffects();
	const [removeFavouriteMutation] = useRemoveFavouriteMutation();
	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const listItems = useAppSelector(selectFavourites);

	const handleRemoveFavouriteBtnClick = (planId: number) => {
		removeFavouriteMutation(planId);
	};

	return <PageWrapper disableFooter={!listItems.length}>
		{!listItems.length ? <Box sx={{height: screenHeightMinusHeaderStyle(isSmUp)}} display='flex' alignItems='center' justifyContent='center'>
			<span>
				Keep discovering !
			</span>
		</Box> :
			<Grid2 container spacing={8}>
				<Grid2 size={12}>
					<Divider sx={{mt: 1}}/>
					<PlanList>
						{listItems.map((item) => <PlanListItemEl 
							key={item.id}
							item={item}
							isFavourited
							onRemoveFavouriteBtnClick={() => handleRemoveFavouriteBtnClick(item.id)}
						/>)}
					</PlanList>
				</Grid2>
			</Grid2>}
	</PageWrapper>;
};

export default DiscoverView;
