import React, { useEffect, useMemo, useRef, useState } from 'react';
import { useNavigate, useParams } from 'react-router-dom';
import { ImageListItem, ImageList, useMediaQuery, useTheme, Grid2, Typography, Tab, Tabs, Box, IconButton, ListItemText, List, ListItem, ListItemIcon, Avatar, Card, Button, Drawer, TextField, Tooltip } from '@mui/material';
import AccessTimeIcon from '@mui/icons-material/AccessTime';
import MoneyIcon from '@mui/icons-material/Money';
import SentimentVerySatisfiedIcon from '@mui/icons-material/SentimentVerySatisfied';
import ShoppingCartCheckoutIcon from '@mui/icons-material/ShoppingCartCheckout';
import DoneIcon from '@mui/icons-material/Done';
import CloseIcon from '@mui/icons-material/Close';
import { selectPlanAuthor, selectPlanDetails, selectPlanImages, selectPlanResourcesMaterials, selectPlanResourcesTools, selectPlanVendors, selectPlanVideos } from './planSlice';
import { Favorite as FavoriteIcon, FavoriteBorder as FavoriteBorderIcon } from '@mui/icons-material';
import PageWrapper from '../components/PageWrapper';
import useAuth, { useAuthEffects } from '../components/Auth';
import YouTubeEmbed from '../components/YouTubeEmbed';
import { useAppSelector } from '../hooks';
import { usePlanDetailsLoadMutation, useAddFavouriteMutation, useRemoveFavouriteMutation } from '../services/plans';
import { useOrderSubmitMutation } from '../services/orders';
import { selectFavouriteIds } from './planSlice';
import { VendorCardV2 } from '../components/VendorCard';
import { selectMyCompany } from './accountSlice';
import { ResourceProductsContainer } from '../components/ResourceProductsContainer';
import { ResourceListItem } from '../types';

function srcset(image: string, size: number, rows = 1, cols = 1) {
	return {
		src: `${image}?w=${size * cols}&h=${size * rows}&fit=crop&auto=format`,
		srcSet: `${image}?w=${size * cols}&h=${
			size * rows
		}&fit=crop&auto=format&dpr=2 2x`,
	};
}

interface TabPanelProps {
children?: React.ReactNode;
index: number;
value: number;
}
  
function CustomTabPanel(props: TabPanelProps) {
	const { children, value, index, ...other } = props;
	return (
		<div
			role="tabpanel"
			hidden={value !== index}
			id={`simple-tabpanel-${index}`}
			aria-labelledby={`simple-tab-${index}`}
			{...other}
		>
			{value === index && <Box sx={{ mt: 3 }}>{children}</Box>}
		</div>
	);
}

function a11yProps(index: number) {
	return {
		id: `simple-tab-${index}`,
		'aria-controls': `simple-tabpanel-${index}`,
	};
}

interface PlanViewResourceListProps {
	resources: ResourceListItem[];
	selectedResources: Set<number>;
	isVendorSelected: boolean;
	onResourceClick: (resourceId: number) => void; 
}

const PlanViewResourceList = ({resources, selectedResources, isVendorSelected, onResourceClick}: PlanViewResourceListProps) => {
	return <Grid2 container spacing={1} mt={1}>
		{resources.map((resource, i) => {
			return <Grid2 size={12} key={i}>
				<ResourceProductsContainer
					resource={resource}	
					products={[]}
					readOnly
					{...(isVendorSelected ? {
						isResourceChecked: selectedResources.has(resource.id),
						onResourceCheckboxChange: () => true,
						onResourceClick: () => onResourceClick(resource.id)
					} : {})}
				/>
			</Grid2>;
		})}
	</Grid2>;
};

const PlanView = () => {
	useAuthEffects();

	const theme = useTheme();
	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));
	const [value, setValue] = React.useState(0);
	const materialsListRef = useRef<any>(null);
	const { id } = useParams();
	const {userProfile} = useAuth();
	
	const [planDetailsLoadMutation] = usePlanDetailsLoadMutation();
	const [orderSubmitMutation, { isSuccess, data }] = useOrderSubmitMutation();
	const [addFavouriteMutation] = useAddFavouriteMutation();
	const [removeFavouriteMutation] = useRemoveFavouriteMutation();
	const navigate = useNavigate();
	const plan = useAppSelector(selectPlanDetails);
	const vendorsData = useAppSelector(selectPlanVendors);
	const productImages = useAppSelector(selectPlanImages);
	const instructionsVideos = useAppSelector(selectPlanVideos);
	const companyData = useAppSelector(selectPlanAuthor);
	const materialsData = useAppSelector(selectPlanResourcesMaterials);
	const toolsData = useAppSelector(selectPlanResourcesTools);
	const favouriteIds = useAppSelector(selectFavouriteIds);
	const myCompany = useAppSelector(selectMyCompany);

	const handleChange = (event: React.SyntheticEvent, newValue: number) => {
		setValue(newValue);
	};
	
	const [selectedResources, setSelectedResources] = useState(new Set<number>());
	const [vendorMessage, setVendorMessage] = useState('');
	const selectedItemsCount = useMemo(() => selectedResources.size, [selectedResources]);

	const [selectedVendorId, setSelectedVendorId] = useState<number | null>(null);
	const selectedVendor = useMemo(() => vendorsData.find((item) => item.id === selectedVendorId), [selectedVendorId]);

	useEffect(() => {
		if (id) {
			planDetailsLoadMutation(id);
		}
	}, [id]);

	useEffect(() => {
		if (materialsData && selectedVendorId) {
			setSelectedResources(materialsData.reduce((acc, material) => acc.add(material.id), new Set<number>()));
		}
	}, [materialsData, selectedVendorId]);

	useEffect(() => {
		if (isSuccess) {
			navigate(`/purchases/${data.id}`);
		}
	}, [isSuccess, data]);

	const handleResourceCheckboxClick = (materialId: number) => {
		if (selectedResources.has(materialId)) {
			selectedResources.delete(materialId);
		} else {
			selectedResources.add(materialId);
		}
		setSelectedResources(new Set(selectedResources));
	};

	const handleVendorClick = (vendorId: number) => {
		if (vendorId !== selectedVendorId) {
			setSelectedVendorId(vendorId);
		}
		if (materialsListRef.current) {
			materialsListRef.current.scrollIntoView({ behavior: 'smooth', block: 'start' });
		}
	};

	const handleSelectedVendorBtnClick = (e: any) => {
		e.stopPropagation(); setSelectedVendorId(null);
	};

	const handleOrderSubmit = () => {
		if (userProfile && plan) {
			const resources = [...materialsData, ...toolsData].filter(({id}) => selectedResources.has(id));
			orderSubmitMutation({
				planId: plan.id,
				vendorId: selectedVendorId,
				resources: resources.map((resource) => ({ id: resource.id, quantity: resource.quantity })),
				message: vendorMessage,
			});
		}
	};
	
	const handleVendorMessageChange = (e: React.ChangeEvent<HTMLInputElement>) => {
		setVendorMessage(e.target.value);
	};

	const handleAddFavouriteBtnClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, planId: number) => {
		e.stopPropagation();
		if (!userProfile) {
			return;
		}
		addFavouriteMutation({ planId });
	};

	const handleRemoveFavouriteBtnClick = (e: React.MouseEvent<HTMLButtonElement, MouseEvent>, planId: number) => {
		e.stopPropagation();
		removeFavouriteMutation(planId);
	};

	const DrawerHeightEl = () => <Box sx={{height: isSmUp ? 145 : 196}}></Box>;

	return plan && <PageWrapper>
		<Box display={isSmUp ? 'flex' : undefined}>
			<Box flex={isSmUp ? 1 : undefined}>
				<Typography variant='h2' sx={{flex:1}}>{plan.title}</Typography>
				<Typography variant='caption'>by @{companyData?.name}</Typography>
			</Box>
			{isSmUp && <Box>
				<IconButton 
					size='large'
					onClick={(e) => favouriteIds.has(plan.id) ? handleRemoveFavouriteBtnClick(e, plan.id) : handleAddFavouriteBtnClick(e, plan.id)}
				>
					{favouriteIds.has(plan.id) ? <FavoriteIcon color='primary' /> : <FavoriteBorderIcon color='disabled' />}
				</IconButton>
			</Box>}
		</Box>
		<Box sx={{ borderBottom: 1, borderColor: 'divider' }} ref={materialsListRef}>
			<Tabs value={value} onChange={handleChange} aria-label="basic tabs example">
				<Tab label="Plan" {...a11yProps(0)} />
				{/* <Tab label="Instructions" {...a11yProps(1)} /> */}
				<Tab label="Supplies" {...a11yProps(1)} />
			</Tabs>
		</Box>
		<CustomTabPanel value={value} index={0}>
			<Grid2 container direction='row' spacing={2}>
				<Grid2 size={isSmUp ? 6 : 12}>
					<Typography variant='body1'>{plan.description}</Typography>
					<List>
						<ListItem disablePadding>
							<ListItemIcon sx={{mr:2, minWidth: 0}}>
								<MoneyIcon />
							</ListItemIcon>
							<ListItemText primary={plan.materialsCost} />
						</ListItem>
						<ListItem disablePadding>
							<ListItemIcon sx={{mr:2, minWidth: 0}}>
								<AccessTimeIcon />
							</ListItemIcon>
							<ListItemText primary={plan.timeCost} />
						</ListItem>
						{plan.ratingsSummary && <ListItem disablePadding>
							<ListItemIcon sx={{mr:2, minWidth: 0}}>
								<SentimentVerySatisfiedIcon />
							</ListItemIcon>
							<ListItemText primary={plan.ratingsSummary} />
						</ListItem>}
					</List>
				</Grid2>
				<Grid2 size={isSmUp ? 6 : 12}>
					<ImageList
						variant="quilted"
						cols={4}
						rowHeight={100}
						sx={{m:0}}
					>
						{productImages.map((productImage, i) => {
							const colsRows = i === 0 ? 4 : 2;
							return (
								<ImageListItem key={productImage.imgUrl} cols={colsRows} rows={colsRows} sx={{borderRadius: 2, overflow: 'hidden', boxShadow: 3}}>
									<img
										{...srcset(productImage.imgUrl, 250, colsRows * 1, colsRows * 1)}
										alt={plan.title}
										loading="lazy"
										style={{overflow:'hidden'}}
									/>
								</ImageListItem>
							);})}
					</ImageList>
				</Grid2>
				<Grid2 size={12}>
					<Typography variant='h4'>How-to</Typography>
				</Grid2>
				{instructionsVideos.map((video) => (<Grid2 size={isSmUp ? 6 : 12} key={video.youtubeId}>
					<YouTubeEmbed videoId={video.youtubeId} title="Sample Video" />
				</Grid2>))}
				{plan.instructions && <Grid2>
					<Typography variant='body1' whiteSpace='pre-wrap'>{plan.instructions}</Typography>
				</Grid2>}
				<Grid2 size={12} sx={{mt:12}}>
					<Box display='flex' flexDirection='column' alignItems='center' sx={{mt: 4}}>
						<Avatar sx={{width:125, height: 125}}></Avatar>
						<Typography variant='h5' sx={{mt:2}}>@{companyData?.name}</Typography>
						<Typography variant='body1' maxWidth={560} sx={{mt:2}}>
							{companyData?.bio ? companyData.bio : '???'}
						</Typography>
					</Box>
				</Grid2>
			</Grid2>
		</CustomTabPanel>
		<CustomTabPanel value={value} index={1}>
			{/* Materials */}
			<Typography variant='body2' sx={{mt:2}}>Materials needed &middot; {materialsData.length}</Typography>
			<PlanViewResourceList 
				resources={materialsData}
				selectedResources={selectedResources}
				isVendorSelected={Boolean(selectedVendorId)}
				onResourceClick={handleResourceCheckboxClick}
			/>
			{/* Tools */}
			<Typography variant='body2' sx={{mt:2}}>Tools needed &middot; {toolsData.length ? toolsData.length : 'none'} </Typography>
			<PlanViewResourceList 
				resources={toolsData}
				selectedResources={selectedResources}
				isVendorSelected={Boolean(selectedVendorId)}
				onResourceClick={handleResourceCheckboxClick}
			/>
			{/* Vendors */}
			{vendorsData.length == 1 ? <Typography variant='body2' sx={{mt:3}}>
				{vendorsData.length} store ships materials to your location
			</Typography> : <Typography variant='body2' sx={{mt:3}}>
				{vendorsData.length} stores ship materials to your location
			</Typography>}
			{myCompany?.roleVendor && <Card elevation={0} sx={{py:2, px:2, mt:2, display:'flex', alignItems: 'center'}}>
				<Typography flex={1}>Thank you for supplying materials for this project! 💚</Typography>
				{myCompany?.roleVendor && <Box display='flex' justifyContent='right'><Button color='inherit'>Edit my portfolio</Button></Box>}
			</Card>}
			<Grid2 container spacing={2} sx={{mt:2}} justifyContent='center'>
				{vendorsData.map((vendor) => (
					<Grid2 key={vendor.id} size={isSmUp ? 4 : 10} justifyItems='center'>
						<VendorCardV2 
							vendor={vendor}
							onClick={() => handleVendorClick(vendor.id)}
							dim={Boolean(selectedVendorId) && selectedVendorId !== vendor.id}
							actions={
								<Button 
									startIcon={vendor.id !== selectedVendorId ? null : <DoneIcon />} 
									size="small" 
									onClick={vendor.id === selectedVendorId ? () => setSelectedVendorId(null) : undefined}>
									{vendor.id === selectedVendorId ? 'Selected' : 'Tap to select'}
								</Button>
							}
						/>
					</Grid2>
				))}
			</Grid2>
			{Boolean(selectedVendorId) && <DrawerHeightEl />}
			<Drawer
				variant='persistent'
				anchor='bottom'
				open={Boolean(selectedVendorId)}
				PaperProps={{
					elevation: 1
				}}
			>
				<Grid2 container spacing={2} padding={2} pb={3}>
					<Grid2 size={isSmUp ? 'grow' : 12}>
						<Box display='flex' alignItems='center'>
							<IconButton onClick={() => setSelectedVendorId(null)}><CloseIcon /></IconButton>
							<Box display='inline'>
								Requesting <Box display='inline' color={theme.palette.primary.main} fontWeight='bold'>{selectedItemsCount}</Box> items from <Box display='inline' color={theme.palette.primary.main} fontWeight='bold'>{selectedVendor?.name}</Box>
							</Box>
						</Box>
						<TextField label='Additional notes to vendor' placeholder='Hi there 👋, ' autoComplete='off' onChange={handleVendorMessageChange} fullWidth sx={{mt:1, maxWidth: 520}} />
					</Grid2>
					<Grid2 size={isSmUp ? 'auto' : 12} textAlign='right'>
						{userProfile?.id ? (<Button onClick={handleOrderSubmit} startIcon={<ShoppingCartCheckoutIcon />} disabled={!selectedItemsCount || !userProfile?.id}>Proceed to order</Button>) :
							(<Tooltip title='Please sign in before placing an order! Use the Account menu on the top-right corner of the page.'>
								<div>
									<Button onClick={handleOrderSubmit} startIcon={<ShoppingCartCheckoutIcon />} disabled={!selectedItemsCount || !userProfile?.id}>Proceed to order</Button>
								</div>
							</Tooltip>)}
					</Grid2>
				</Grid2>
			</Drawer>
		</CustomTabPanel>
	</PageWrapper>;
};

export default PlanView;
