import { PlanListItem } from '../types';
import {api} from './api';

export const plansApi = api.injectEndpoints({
	endpoints: (builder) => ({
		plansListLoad: builder.mutation({
			query: (args: void) => ({
				url: 'plans/discover',
				method: 'GET',
			}),
		}),
		planDetailsLoad: builder.mutation({
			query: (id: string) => ({
				url: `plans/${id}/details`,
				method: 'GET',
			}),
		}),
		loadFavourites: builder.mutation<PlanListItem[], void>({
			query: () => ({
				url: 'favourites',
				method: 'GET',
			}),
			extraOptions: { maxRetries: 1 }
		}),
		addFavourite: builder.mutation<PlanListItem, {planId: number}>({
			query: (data: {planId: number}) => ({
				url: 'favourites',
				method: 'POST',
				body: data,
			}),
			extraOptions: { maxRetries: 1 }
		}),
		removeFavourite: builder.mutation<number, number>({
			query: (planId: number) => ({
				url: `favourites/${planId}`,
				method: 'DELETE',
			}),
			extraOptions: { maxRetries: 1 }
		}),
	}),
});

export const { usePlansListLoadMutation, usePlanDetailsLoadMutation, useLoadFavouritesMutation, useAddFavouriteMutation, useRemoveFavouriteMutation } = plansApi;
