import React, { useEffect, useMemo, useState } from 'react';
import PageWrapper from '../components/PageWrapper';
import { useAuthEffects } from '../components/Auth';
import { useTheme, Avatar, Box, List, ListItem, ListItemAvatar, ListItemButton, ListItemText, Typography, useMediaQuery, Switch, FormControlLabel } from '@mui/material';
import FolderIcon from '@mui/icons-material/Folder';
import { useNavigate } from 'react-router-dom';
import { useAppSelector } from '../hooks';
import dayjs from 'dayjs';
import { screenHeightMinusHeaderStyle } from '../common/util';
import { selectSalesOrdersListItems } from './salesSlice';
import { useSalesOrderListLoadMutation } from '../services/salesOrders';
import { ListViewStatusStepper } from '../components/Stepper';
import { OrderStatus } from '../types';

const OrdersView = () => {
	useAuthEffects();

	const theme = useTheme();
	const navigate = useNavigate();
	const [salesOrderListLoadMutation] = useSalesOrderListLoadMutation();
	const orders = useAppSelector(selectSalesOrdersListItems);
	const [showInactive, setShowInactive] = useState<boolean>(false);

	useEffect(() => {
		salesOrderListLoadMutation();
	}, []);

	const isSmUp = useMediaQuery(theme.breakpoints.up('sm'));

	const activeOrders = useMemo(() => orders.filter(({ status }) => [OrderStatus.Created, OrderStatus.Prepared, OrderStatus.Paid].includes(status)), [orders]);
	const inactiveOrders = useMemo(() => orders.filter(({ status }) => ![OrderStatus.Created, OrderStatus.Prepared, OrderStatus.Paid].includes(status)), [orders]);
	const isEmptyView = !(showInactive ? orders : activeOrders).length;

	return <PageWrapper disableFooter={!orders.length} noScroll={isEmptyView}>
		{Boolean(orders.length) && <Box display='flex' justifyContent='center'>
			<FormControlLabel sx={{userSelect: 'none'}} control={<Switch checked={showInactive} onChange={() => setShowInactive(!showInactive)} />} label='Show inactive orders' />
		</Box>}
		{isEmptyView && <Box sx={{height: screenHeightMinusHeaderStyle(isSmUp)}} display='flex' alignItems='center' justifyContent='center'>
			<span>
				No sales yet !
			</span>
		</Box>}
		{!!activeOrders.length && <Box>
			<Typography variant='body2' sx={{mt:2}}>Active orders</Typography>
			<List>
				{activeOrders.map((order, i) => (
					<ListItem key={i}>
						<ListItemButton onClick={() => navigate(`/sales/${order.id}`)}>
							<ListItemAvatar>
								<Avatar src={order.customerImg} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
									<FolderIcon />
								</Avatar>
							</ListItemAvatar>
							<ListItemText
								primary={<span>{order.itemsCount} items ordered by {order.customerName}</span>}
								secondary={dayjs(order.createdAt).format('MMMM D, HH:mm')}
							/>
							<Box justifyItems='center'>
								<Box>
									{order.status}
								</Box>
								<ListViewStatusStepper orderStatus={order.status} isCancelled={[OrderStatus.Cancelled, OrderStatus.Rejected].includes(order?.status)}/>
							</Box>
						</ListItemButton>
					</ListItem>
				))}
			</List> 
		</Box>}
		{showInactive && inactiveOrders.length && <Box>
			<Typography variant='body2' sx={{mt:2}}>Past orders</Typography>
			<List>
				{inactiveOrders.map((order, i) => (
					<ListItem key={i}>
						<ListItemButton onClick={() => navigate(`/sales/${order.id}`)}>
							<ListItemAvatar>
								<Avatar src={order.customerImg} variant='square' sx={{ width: 96, height: 96, mr: 2, borderRadius: 2 }}>
									<FolderIcon />
								</Avatar>
							</ListItemAvatar>
							<ListItemText
								primary={<span>{order.itemsCount} items ordered by {order.customerName}</span>}
								secondary={dayjs(order.createdAt).format('MMMM D, HH:mm')}
							/>
							<Box justifyItems='center'>
								<Box>
									{order.status}
								</Box>
								<ListViewStatusStepper orderStatus={order.status} isCancelled={[OrderStatus.Cancelled, OrderStatus.Rejected].includes(order?.status)}/>
							</Box>
						</ListItemButton>
					</ListItem>
				))}
			</List> 
		</Box>}
	</PageWrapper>;
};

export default OrdersView;
